import React from 'react';
import SensorScreen from './SensorScreen';
import CssBaseline from '@material-ui/core/CssBaseline';

import AppBar from '@material-ui/core/AppBar';
import Toolbar from '@material-ui/core/Toolbar';
import IconButton from '@material-ui/core/IconButton';
import MenuIcon from '@material-ui/icons/Menu';
import MenuItem from '@material-ui/core/MenuItem';
import Menu from '@material-ui/core/Menu';

import { makeStyles } from '@material-ui/core/styles';
import DialogTitle from '@material-ui/core/DialogTitle';
import Dialog from '@material-ui/core/Dialog';
import List from '@material-ui/core/List';
import { GroupBy, Map } from 'react-lodash';
import NotificationEventsGroup from './NotificationEventsGroup';

const useStyles = makeStyles((theme) => ({
  root: {
    flexGrow: 1,
  },
  menuButton: {
    marginRight: theme.spacing(2),
  },
  title: {
    flexGrow: 1,
  },
  dialog2: {
    minWidth: "400px"
  }
}));

const reloadData = (setNotifictionEvents) => {
  console.log('reloadData');
  fetch(`https://aoq53tdls8.execute-api.us-east-1.amazonaws.com/dev/sensorNotificationEvent`)
  .then(response => response.json())
  .then(json => {
    setNotifictionEvents(json);
  })
  .catch((error) => {
    console.error('ERROR',error);
  });
}

export default function App() {
  const classes = useStyles();
  const [anchorEl, setAnchorEl] = React.useState(null);
  const open = Boolean(anchorEl);

  const handleMenu = (event) => {
    setAnchorEl(event.currentTarget);
    console.log('handleMenu');
  };

  const handleClose = () => {
    setAnchorEl(null);
    console.log('handleClose');
  };

  const [openEvents, setOpenEvents] = React.useState(false);
  const [notificationEvents, setNotifictionEvents] = React.useState([]);

  const handleClickEvents = () => {
    setOpenEvents(false);
    handleClose();
  };

  const handleOpenEvents = () => {
    setOpenEvents(true);
  };

  React.useEffect(() => {
    reloadData(setNotifictionEvents);
  }, []);

  return (
    <React.Fragment>
      <CssBaseline />
      <div className={classes.root}>
      <AppBar position="fixed">
        <Toolbar>
          <IconButton 
            edge="start" 
            className={classes.menuButton} 
            color="inherit" 
            aria-label="menu" 
            aria-controls="menu-appbar"
            aria-haspopup="true"
            onClick={handleMenu}
          >
            <MenuIcon />
          </IconButton>
          <Menu
            id="menu-appbar"
            anchorEl={anchorEl}
            anchorOrigin={{
              vertical: 'top',
              horizontal: 'left',
            }}
            keepMounted
            transformOrigin={{
              vertical: 'top',
              horizontal: 'left',
            }}
            open={open}
            onClose={handleClose}
          >
            <div>
              <MenuItem onClick={handleOpenEvents}>
                Notification Events
              </MenuItem>
              <Dialog onClose={handleClickEvents} aria-labelledby="simple-dialog-title" open={openEvents} fullWidth={true} maxWidth={'xs'}>
                  <DialogTitle id="simple-dialog-title">Notification Events</DialogTitle>
                  <List>
                    <GroupBy collection={notificationEvents} iteratee={(entry) => entry.sensorId}>
                      { (value) => 
                        <Map collection={Object.keys(value)} iteratee={key =>
                          <NotificationEventsGroup 
                            eventsList={value[key]} 
                            handleClickEvents={handleClickEvents} 
                            key={key}
                            snoozeFunction={(sensorId) => console.log('snoozeFunction: ', sensorId)}
                          />
                        } />
                      }
                    </GroupBy>
                  </List>
                </Dialog>
            </div>
          </Menu>
        </Toolbar>
      </AppBar>
      <Toolbar/>
      </div>
      <SensorScreen />
    </React.Fragment>
  );
}